@charset "UTF-8";
body {
  margin: 0;
}

.page {
  scroll-behavior: smooth;
}

@media (min-width: 320px) {
  .page-body {
    font-family: "Ubuntu", sans-serif;
    font-weight: 300;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
    position: relative;
    height: 4229px;
  }
  .page-body::after {
    content: "";
    position: absolute;
    background-image: url(../../img/mobile-intro-bg.jpg);
    background-repeat: no-repeat;
    width: 320px;
    height: 568px;
    z-index: -1;
  }
  .page-body::before {
    content: "";
    position: absolute;
    background-image: url("../../img/index-bg.jpg");
    width: 320px;
    height: 100%;
    background-attachment: fixed;
    background-size: cover;
    margin-top: 568px;
    z-index: -1;
  }
}
@media (min-width: 320px) {
  .page-header {
    display: flex;
    width: 320px;
    padding-top: 22px;
    position: fixed;
    z-index: 3;
  }

  .page-header--opened-nav {
    background-image: url("../../img/mobile-nav-bg.jpg");
  }

  .page-header__logo {
    width: 28px;
  }

  .page-header__button {
    width: 108px;
    height: 34px;
  }

  .page-header__nav-toggle {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    position: relative;
    width: 24px;
    background-color: #FFFFFF;
    height: 2px;
    box-shadow: 0 6px 0 #FFFFFF;
    margin-left: 62px;
    margin-top: 7px;
  }
  .page-header__nav-toggle::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: #FFFFFF;
    top: 12px;
    left: 8px;
  }

  .page-header__nav-toggle--close {
    box-shadow: none;
    transform: rotate(45deg);
    top: 6px;
  }
  .page-header__nav-toggle--close::after {
    width: 24px;
    left: 0;
    top: 0;
    transform: rotate(90deg);
  }

  .page-header--scrolled {
    background-color: #000000;
    padding-bottom: 20px;
    border-bottom: 1px solid #0DD1A2;
  }
}
@media (min-width: 320px) {
  .logo {
    background-image: url("../../img/digpl-logo.svg");
    background-repeat: no-repeat;
    margin-left: 20px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

